<template>
  <div>
    <v-btn @click="refresh" id="btn-gallery">Refresh</v-btn>
    <v-img
        v-for="(img) in imgs"
        :key="img"
        id="img"
        :src="img"
    ></v-img>

    <v-pagination
      v-model="page"
      :length="6"
    ></v-pagination>
  </div>
</template>

<script>
import axios from '../main'
export default {
  data: () => ({
    page: 1,
    imgs: [require('@/assets/profile/aurora.jpg'), require('@/assets/profile/cloud-blu.jpg'), 
            require('@/assets/profile/cloud-org.jpg'), require('@/assets/profile/keyboard.jpg')],
  }),
  methods: {
    refresh () {
      this.axios.get("/img")
      .then(response => {
        this.imgs = response.data;
        this.imgs.forEach((item, index, array) => {
          item = this.axios.defaults.baseURL + item;
          array[index] = item;
        })

      })
      .catch(response => {
        console.log(response);
      })
    }
  },
  beforeRouteEnter (to, from, next) {
		axios.get("/img")
      .then(response => {
        next(vm => {
          vm.imgs = response.data;
          vm.imgs.forEach((item, index, array) => {
            item = axios.defaults.baseURL + item;
            array[index] = item;
          })
        })
      })
      .catch(response => {
        console.log(response);
      })
  }
}
</script>

<style lang='scss'>
#img {
    max-width: 90vw;
    height: auto;
    margin-left: 5vw;
    margin-top: 6vh;
}

#btn-gallery {
  margin-left: 40vw;
  margin-top: 5vh;
}
</style>